___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --border: 3px solid gray;
}

.container {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    justify-content: center;
    align-items: center;
    flex-grow: 1;
}

.container__item {
    box-sizing: border-box;
    max-width: 600px;
    padding: 20px;
    border-radius: 30px;
    border: var(--border);
    width: 100%;
    margin-top: 10px;
    margin-bottom: 10px;
}

.content {
    display: flex;
    justify-content: center;
    flex-direction: column;
    font-size: 25px;
    font-family: sans-serif;
}

.content__item {
    border: var(--border);
    padding: 10px 15px;
    margin-bottom: 20px;
}

.fieldset {
    border: var(--border);
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 5px;
}

.fieldset__item {
    margin: 5px;
    font-size: 25px;
    border: var(--border);
    border-radius: 30px;
    padding: 2px 10px;
}

.calendar {
    margin-bottom: 15px;
}

.calendar__header {
    text-align: center;
    margin: 0;
}

.calendar__content {
    border: var(--border);
}

.calendar__row {
    display: flex;
    justify-content: space-around;
    flex-wrap: nowrap;
    cursor: pointer;
    min-height: 35px;
}

.calendar__row--main {
    cursor: auto;
}

.calendar__row-item {
    border: 1px solid gray;
    flex: 1 1 0px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.calendar__row-item--weekend {
    background-color: red;
}

.legend {
    font-size: 15px;
    border: var(--border);
    border-radius: 30px;
    padding: 20px;
}

.legend__header {
    font-size: 20px;
    font-weight: bold;
    text-align: center;
    margin-bottom: 10px;
}

.legend__item {
    display: flex;
    align-items: center;
    padding: 5px 10px;
    margin: 5px;
}

.legend__icon {
    display: inline-block;
    height: 30px;
    margin-right: 10px;
    border: none;
    padding: 0;
    box-shadow: none;
    flex-basis: 40px;
    flex-grow: 0;
    flex-shrink: 0;
}

.warning-message {
    font-size: 20px;
    padding: 15px;
    color: red;
    border: var(--border);
    border-radius: 30px;
}

.user-selector-container {
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
}

.user-selector__select {
    font-size: 30px;
    text-align: center;
    border: 2px solid gray;
}

.hidden-element {
    display: none;
}
